/* ------------------------ Nav Bar styling ----------------------------------------------------------------- */
.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 12rem;
  align-items: center;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  position: sticky;
  top: 0;
  background-color: white;
  border-bottom: 1px solid rgb(196, 196, 196);
  z-index: 3;
}

.title-container {
  display: flex;
  margin-left: 3%;
  width: 25%;
  height: 100%;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  letter-spacing: 0.3rem;
}
.title {
  display: flex;
  box-sizing: border-box;
}

.title h1 {
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  font-size: 3rem;
}

.button-container-desktop {
  display: flex;
  justify-content: flex-end;
  margin-right: 3%;
  width: 55%;
  height: 100%;
  letter-spacing: 0.2rem;
  text-align: center;
}
.button {
  display: flex;
  flex-direction: column;
  height: 100%;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin: 0 0;
}

.button .dropdown {
  position: absolute;
  bottom: -180px;
  display: none;
  flex-direction: column;
  overflow: visible;
  border: 1px solid black;
  align-content: left;
}

.button svg {
  transition: all 0.3s ease-out;
}

.button:hover svg {
  transform: scaleY(-1);
}
.button .line {
  width: 0;
  box-sizing: border-box;
  border-bottom: 0.5rem solid;
  transition: all 0.3s ease-out;
}

.button:hover .line {
  border-bottom: 0.5rem solid black;
  width: 100%;
}
.button a {
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  font-size: 2rem;
  font-weight: bolder;
  margin: 0 2rem;
}

@media screen and (max-width: 820px) {
  .title h1 {
    font-size: 2.3rem;
  }
  .button a {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 1650px) {
  .button-container-desktop {
    display: none;
  }
}

/* ------------------------ Home styling ----------------------------------------------------------------- */
.home-container {
  display: flex;
  flex-direction: column;
  font-size: 120%;
  font-family: "Montserrat", sans-serif;
}

.home-container h1 {
  font-size: 3rem;
  letter-spacing: 0.3rem;
  font-weight: 700;
}
.home-container h2 {
  font-size: 2rem;
  letter-spacing: 0.2rem;
  margin-bottom: 2.5rem;
  font-weight: 700;
}

.home-container p {
  font-size: 1.5rem;
  line-height: 1.5;
  font-weight: 300;
}

.home-container li {
  line-height: 2;
}

.home-container a {
  font-weight: 500;
}

@media screen and (max-width: 820px) {
  .home-container h1 {
    font-size: 2.5rem;
    letter-spacing: 0.25rem;
  }
  .home-container h2 {
    font-size: 1.8rem;
    letter-spacing: 0.15rem;
    margin-bottom: 1.5rem;
  }

  .home-container p {
    font-size: 1.3rem;
    line-height: 1.5;
  }
}

@media screen and (max-width: 1400px) {
  .home-container {
    margin: 0 0;
  }
}
/* ------------------------ Banner 1 styling ----------------------------------------------------------------- */

.banner1-container {
  display: flex;
  padding: 8rem 0;
  background-color: #1e6dd9;
  color: white;
}

.banner1-container .sub-container1 {
  gap: 1rem;
  flex-direction: column;
  text-align: left;
  margin: 0 15%;
}

.banner1-container p.top-text {
  font-size: 1.8rem;
  margin-bottom: 0.8rem;
}
.banner1-container .explore-button {
  display: flex;
}

.banner1-container .explore-button a {
  text-decoration: none;
  margin: 2rem 0;
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  font-size: 1.4rem;
  font-weight: 700;
  transition: background-color 0.3s;
  color: white;
  background-color: #07db5e;
}

.banner1-container .explore-button a:hover {
  background-color: #55f823;
}

.banner1-container h1 {
  margin-top: 0;
  font-size: 4.2rem;
}

.explore-button a {
}

@media screen and (max-width: 820px) {
  .banner1-container {
    margin: 0;
  }
}

/* ------------------------ Banner 2 styling -----------------------------------------------------------------*/

.banner2-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  padding: 8rem 0;
}

.banner2-container .sub-container1 {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 0 15%;
}

.banner2-container .sub-container2 {
  display: flex;
  justify-content: space-between;
  gap: 2.5rem;
  text-align: left;
  margin: 0 15%;
}

.banner2-container .boxes {
  border: 1px;
  border-radius: 2.5rem;
  padding: 2rem;
  box-sizing: border-box;
  transition: background-color 0.3s;
}

.banner2-container .boxes:hover {
  border-radius: 2.5rem;
  box-sizing: border-box;
  background-color: rgb(219, 219, 219);
  box-shadow: 0 0 5px 0 rgb(219, 219, 219);
}

@media screen and (max-width: 820px) {
  .banner2-container {
    margin: 0;
    gap: 3rem;
  }

  .banner2-container .sub-container2 {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 1.5rem;
    text-align: left;
    margin: 0 5%;
  }
  .banner2-container .boxes {
    border: 1px solid rgb(201, 201, 201);
    padding: 2rem;
    margin: 0;
  }
}

/*------------------------ Property card styling ------------------------- */
.propertyCard {
  border-radius: 2.5rem;
  transition: background-color 0.3s ease-out, color 0.3s ease-out;
  box-sizing: border-box;
  flex-grow: 1;
  width: calc(100% / 3.5);
}

.propertyCard a {
  text-decoration: inherit;
  color: inherit;
}

.propertyCard a:hover {
  text-decoration: inherit;
  color: inherit;
}

.propertyCard:hover {
  background-color: rgb(73, 73, 73);
  cursor: pointer;
  color: rgb(233, 233, 233);
  box-shadow: 0 0 5px 0 rgb(73, 73, 73);
}

.propertyCard .property-image-box {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 2.5rem;
  border-top-right-radius: 2.5rem;
  position: relative;
}

.propertyCard .property-image-box svg {
  position: absolute;
  opacity: 0%;
  color: white;
  transition: all 0.3s ease-out;
  z-index: 2;
}

.propertyCard:hover .property-image-box svg {
  opacity: 100%;
}

.propertyCard .property-image-box img {
  width: 100%;
  border-top-left-radius: 2.5rem;
  border-top-right-radius: 2.5rem;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
}

.propertyCard:hover .property-image-box img {
  opacity: 50%;
}

.propertyCard .sub-box1,
.sub-box2 {
  margin: 0 2rem;
}

@media screen and (max-width: 1400px) {
  .propertyCard {
    width: auto;
  }
}

/* ------------------------ Banner 3 styling ----------------------------------------------------------------- */
.banner3-container {
  display: flex;
  padding: 8rem 0;
  background-color: #2eecf0;
  flex-direction: column;
  gap: 2.5rem;
}

.banner3-container .sub-container1 {
  display: flex;
  flex-direction: row;
  margin: 0 15%;
}

.banner3-container .sub-container2 {
  display: flex;
  justify-content: space-around;
  margin: 0 15%;
  gap: 2.5rem;
  flex-direction: row;
  flex-wrap: wrap;
}

.banner3-container .sub-container2 .sub-box1,
.sub-box2 {
  margin: 0 2rem;
}

@media screen and (max-width: 820px) {
  .banner3-container {
    margin: 0;
  }
  .banner3-container .sub-container2 {
    display: flex;
    justify-content: space-around;
    margin: 0 5%;
    flex-wrap: wrap;
    gap: 3rem;
  }
}
/* ------------------------ Banner 4 styling ----------------------------------------------------------------- */

.banner4-container {
  display: flex;
  padding: 8rem 0;
  justify-content: center;
  background-color: #63b1ff;
}

.banner4-container .sub-container1 {
  border: 0;
}

/* ------------------------ Footer styling ----------------------------------------------------------------- */

.footer-container {
  display: flex;
  flex-direction: column;
  background-color: rgb(209, 209, 209);
  padding: 3% 0;
  gap: 2.5rem;
  font-family: "Montserrat", sans-serif;
}

.footer-banner1 {
  display: flex;
  flex-direction: row;
  margin: 0 5%;
  gap: 2.5rem;
}

.footer-banner1 .box1 {
  width: 33.33%;
  border-right: 1px solid black;
  padding-right: 30px;
}

.footer-banner1 .box2 {
  display: flex;
  flex-direction: column;
  width: 33.33%;
}

.footer-banner1 .box2 .banner2 {
  display: flex;
  flex-direction: row;
}

.footer-banner1 .box2 .banner2 .banner2-left {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.footer-banner1 .box2 .banner2 .banner2-right {
  width: 30%;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.footer-banner1 .box2 .banner2 .banner2-right svg:hover {
  cursor: pointer;
}

.footer-banner1 .box2 .banner2 .banner2-left .info-box {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}

.footer-banner1 .box2 .banner2 .banner2-left .info-box svg {
  margin-right: 1rem;
}

.footer-banner1 .box2 .banner2 .banner2-left .info-box p {
  margin: 0;
}
.footer-banner1 .box2 .banner2 .banner2-left .info-box a {
  margin: 0;
  text-decoration: none;
}

.footer-banner1 .box3 {
  width: 33.33%;
  border-left: 1px solid black;
  padding-left: 30px;
}
.footer-banner2 {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.footer-banner2 .box1 {
  display: flex;
  width: 100%;
  flex-direction: column;
  text-align: center;
}

.footer-banner2 .box2 {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 1.5rem;
}

.footer-banner2 .box2 form {
  width: 1200px;
  height: 80px;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 2rem;
  gap: 2px;
}

.footer-banner2 .box2 form input,
button {
  box-sizing: border-box;
  width: calc(100% / 3);
  align-items: stretch;
  height: 100%;
  border: 0;
}

.footer-banner2 .box2 form #name {
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  font-size: larger;
  font-weight: 700;
  padding-left: 20px;
}

.footer-banner2 .box2 form #email {
  font-size: larger;
  font-weight: 700;
  padding-left: 20px;
}

.footer-banner2 .box2 form #subscribe {
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  background-color: rgb(49, 49, 49);
  color: white;
  font-size: larger;
  font-weight: 700;
  transition: all 0.3s ease-out;
}

.footer-banner2 .box2 form #subscribe:hover {
  background-color: white;
  color: black;
  cursor: pointer;
}

